.Title {
	font-size: var(--font-xxl);
	color: var(--white);
	letter-spacing: 0.1em;
	font-weight: 200;
	text-shadow: var(--darkShadow);
	text-transform: uppercase;
	line-height: 1;
}

svg {
	fill: var(--font-color);
}

.svg-lg {
	svg {
		height: var(--space-xxl);
		width: var(--space-xxl);
	}
}

.svg-md {
	svg {
		height: var(--space-xl);
		width: var(--space-xl);
	}
}

.svg-sm {
	svg {
			height: var(--space-lg);
		width: var(--space-lg);
	}
}

.icon_BTN,
.icon_BTN:focus,
.icon_BTN:hover,
.icon_BTN:active {
	cursor: pointer;
	border: none;
	margin: 0;
	padding: 0;
	background: transparent;
	outline: none;
}

.btn {
	--btn-color: var(--white);
	-btn-background: transparent;
	padding: var(--space) var(--space-lg);
	border: 2px solid var(--btn-color);
	color: var(--btn-color);
	transition: var(--transition);
	border-radius: 2px;
	font-size: var(--font);
	text-transform: uppercase;
	background: var(--btn-background);
	letter-spacing: 2px;

	&:hover {
		--btn-color: var(--brand4);
		--btn-background: #fffd;
	}
}

.link {
	font-style: italic;
	border-bottom: 2px solid #fff;
}

